@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
		Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.form__photo-preview {
	height: 50px;
	width: 50px;
	border: 1px solid;
}

* {
	box-sizing: border-box;
}
body {
	margin: 0;
}
.home-container {
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	/* background-color: #f5f5f5; */
}
.header {
	-webkit-align-items: center;
	        align-items: center;
	height: 140px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
}
.header-logo {
	color: #114e4e;
}

.main-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}
.home-title {
	font-size: 24px;
	color: #e17334;
	text-align: center;
	margin: 5px 14px 0 13px;
	font-family: Merriweather, serif;
}

.home-subtitle {
	margin: 0 0 17px;
	font-family: Open Sans, sans-serif;
	font-size: 14px;
	color: #114e4e;
	text-align: center;
}

.home__list {
	list-style: none;
	padding: 0;
	width: 268px;
	height: 74px;
	margin-top: 20px;
	border: solid 2px rgba(225, 115, 52, 0.21);
	border-radius: 4px;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
}

.home__list-item {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
}

.home__list-item svg {
	width: 32px;
	height: 32px;
	color: #e17334;
	font-size: 23px;
}

.home__list-text {
	margin: 0;
	font-family: Open Sans, sans-serif;
	text-align: center;
	color: #54585a;
	font-size: 13px;
}

/*Button*/

.home-button {
	margin: 60px 0;
	text-decoration: none;
	width: 129px;
	height: 42px;
	border-radius: 21px;
	background: #438792;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
.home-button:hover {
	background-color: #e17334;
	transition: 0.4s ease-in-out;
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}
.home-button span {
	color: #fff;
	font-family: Open Sans, sans-serif;
	font-weight: 600;
	font-size: 16px;
	text-transform: uppercase;
	text-align: center;
}
footer {
	position: fixed;
	bottom: 0;
	width: 100%;
	text-align: center;
	padding: 10px 0;
}
footer a {
	color: #6b6b6b;
}
/*tablet*/
@media screen and (min-width: 768px) {
	.header-logo {
		font-size: 40px;
	}
	.home-title {
		font-size: 26px;
	}

	.home-subtitle {
		font-size: 17px;
		width: 350px;
	}

	.home__list {
		width: 325px;
		height: 86px;
	}

	.home__list svg {
		font-size: 32px;
	}
	.home__list-text {
		font-size: 14px;
	}
	footer {
		padding: 15px 0;
	}
}

:root {
	--first-color-light: #a2deaf;
	--second-color: #420101;
}

.header-card {
	margin: 0 20px;
}
.main-card {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column-reverse;
	        flex-direction: column-reverse;
	margin: 0 20px;
}

.preview__card--social {
	height: 40px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}
.preview__card--ul {
	list-style: none;
	margin: 0;
	padding: 0;
}
.social-card-li {
	width: 32px;
	height: 32px;
	border-radius: 16px;
	margin: 0 10px 0 0;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
.preview__card.preview__card.preview-default .social-card-link {
	color: #114e4e;
}
.preview__card.preview__card.preview-second .social-card-link {
	color: #420101;
	color: var(--second-color);
}

.social-card-icon {
	font-size: 19px;
}
/* -------------- */

.preview__card {
	height: 457px;
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
.preview__card.preview-default {
	background: linear-gradient(180deg, #bfcdd0, #dde9ed);
}
.preview__card.preview-second {
	background: linear-gradient(180deg, rgba(233, 87, 38, 0.3), white);
}
.preview__card.preview-third {
	background: linear-gradient(180deg, rgba(34, 6, 192, 0.2), white);
}

.reset-btn {
	position: absolute;
	left: 10px;
	top: 10px;
	text-transform: uppercase;
	width: 75px;
	height: 30px;
	background-color: transparent;
	border: 1px solid #fff;
	border-radius: 15px;
	text-align: center;
	cursor: pointer;
	color: #54585a;
	font-family: Open Sans, sans-serif;
	font-weight: 400;
	font-size: 12px;
}
.reset-icon {
	margin-right: 3px;
}
.pantone-container {
	width: 275px;
	height: 330px;
	background-color: #fff;
	padding: 0 15px 10px;
}

.preview__card--text {
	background: #fff;
	margin-top: 15px;
	margin-bottom: 5px;
	word-wrap: break-word;
	max-width: 100%;
	box-sizing: border-box;
	padding-left: 10px;
}
.preview__card.preview-default .preview__card--text {
	border-left: 5px solid #a2deaf;
}
.preview__card.preview-default .card-subtitle {
	color: #54585a;
}
.preview__card.preview-second .card-subtitle {
	color: #bd1010;
}
/*  */
.preview__card.preview-second .preview__card--text {
	border-left: 4px solid #e95626;
}
.preview__card.preview-third .preview__card--text {
	border-left: 4px solid rgba(34, 6, 192, 0.5);
}
.card-title,
.card-subtitle {
	margin: 0;
}
.card-title {
	text-transform: capitalize;
	font-weight: 700;
	font-size: 22px;

	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}
.card-subtitle {
	font-weight: 400;
	font-size: 16px;
	padding-top: 4px;
}
.preview__card--image {
	width: 100%;
	height: 205px;
	background: #fff;
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: cover;
	border-radius: 2px;
}
.preview__card--image.big {
	height: 230px;
}

.card-footer {
	position: static;
	padding: 20px 0;
}

@media screen and (min-width: 768px) {
	.main-card {
		-webkit-flex-direction: row;
		        flex-direction: row;
	}
	form {
		width: 40%;
	}
	.preview__card {
		width: 100%;
		height: 530px;
		border-radius: 6px;
	}
	.card-footer {
		position: fixed;
	}
}
/*  */
.hide {
	display: none;
}

.design__form {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
/* designgngngn */
.design__subtitle {
	margin-top: 0;
	font-family: 'Open Sans', sans-serif;
	font-size: 14px;
	color: #54585a;
}
.desing-colors-list {
	padding: 0;
}
.radio_btn {
	margin-right: 10px;
}
.design-form-li {
	list-style: none;
	display: -webkit-flex;
	display: flex;
	margin-bottom: 20px;
}
.theme {
	list-style: none;
	margin: 0;
	padding: 0;
	display: -webkit-flex;
	display: flex;
}
.theme__color {
	width: 30px;
	height: 25px;
}

/* fill relleanr */
.form_title {
	font-family: Open Sans, sans-serif;
	color: #114e4e;
	font-size: 14px;
	line-height: 20px;
}
.form_field {
	display: block;
	border: 1px solid #a2deaf;
	width: 100%;
	height: 30px;
	margin: 6px 0 20px 0;
}
.form-photo-preview {
	border: 1px solid #a2deaf;
	height: 30px;
	width: 30px;
	margin-left: 20px;
	background-size: cover;
}
.form__container-btn-photo {
	display: -webkit-flex;
	display: flex;
	margin: 6px 0 20px;
}
.form-label-photo {
	font-family: Open Sans, sans-serif;
	color: #114e4e;
	font-size: 14px;
	line-height: 20px;
	border-style: none;
	border-radius: 3px;
	width: 140px;
	height: 30px;
	background: #e17334;
	color: #fff;
	box-shadow: 0 0 4px 0 rgb(0 0 0 / 32%);
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor: pointer;
}
.form-file-input {
	display: none;
}

/* share */
.download-btn {
	width: 199px;
	height: 50px;
	font-size: 18px;
	color: #fff;
	font-family: Open Sans, sans-serif;
	font-weight: 700;
	text-transform: uppercase;
	background-color: #e17334;
	box-shadow: 0 1px 6px 0 rgb(0 0 0 / 32%);
	border: none;
	cursor: pointer;
	border-radius: 12px;
	margin-bottom: 15px;
}
.download-btn:hover {
	top: 1px;
	box-shadow: 0 5px grey;
	position: relative;
}
.download-btn svg {
	margin-right: 8px;
}
.download__form {
	opacity: 1;
	visibility: visible;
	width: 100%;
	height: 90px;
	padding-top: 20px;
	transition: all 0.5s;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

form {
	margin-top: 25px;
}
fieldset {
	font-family: Open Sans, sans-serif;
	border: none;
	border-bottom: 1px solid #d3d3d3;
}
.custom-container-title {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin: 10px 0 10px 0;
	cursor: pointer;
	height: auto;
}
.custom-logo-ctn {
	width: 28px;
	height: 28px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
.custom-logo-ctn svg {
	color: orange;
	height: 100%;
	width: 100%;
}
.custom-title {
	width: 100%;
	padding-left: 10px;
	margin: 0;
	font-family: 'Open Sans', sans-serif;
	color: gray;
	text-transform: uppercase;
	font-size: 18px;
}

.custom-arrow-ctn {
	width: 28px;
	height: 28px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
@-webkit-keyframes spin-up {
	from {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}
	to {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
}
@keyframes spin-up {
	from {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}
	to {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
}
.custom-arrow-ctn svg {
	-webkit-animation-name: spin-up;
	        animation-name: spin-up;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	color: green;
	height: 100%;
	width: 100%;
}

